main > .campaign {
    @include media('>=small') {
        .wrapper--pathway,
        .ksp--pathway {
            grid-column: 2/-2;
        }
    }

    @include media('<small') {
        .wrapper--pathway {
            padding: 0 $measure--small2;
            grid-column: 1/-1;
        }
        .pathway,
        .banner {
            grid-column: 1/-1;
        }
    }

    // Generic
    .ksp-block,
    .wrapper--ksp-block {
        grid-column: 1/-1;
    }
}
